import { LoaderService } from './../../../shared/services/loader.service';
import { Component, OnInit } from '@angular/core';
import {
  Order,
  OrderItem,
  OrderResult,
  PaymentType,
  ProductType,
} from 'src/app/@core/models/data-objects';
import { AuthService } from '../../../../@core/services/auth/auth.service';
import { SystemService } from '../../../../@core/services/system/system.service';
import { Observable } from 'rxjs';
import { OrderService } from '../orders/order.service';
import { SwalComponent } from '@sweetalert2/ngx-sweetalert2';
import { TranslateService } from '@ngx-translate/core';

interface Item {
  item: string;
  quantity: string;
  price: string;
}

@Component({
  selector: 'app-making-order',
  templateUrl: './making-order.component.html',
  styleUrls: ['./making-order.component.scss'],
})
export class MakingOrderComponent implements OnInit {
  orderResult: OrderResult = new OrderResult();
  order: Order = new Order();
  orderItem: OrderItem = new OrderItem();
  orderItems: Array<OrderItem> = new Array<OrderItem>();
  paymentTypes$: Observable<PaymentType[]>;
  productTypes$: Observable<ProductType[]>;
  markers: google.maps.Marker[] = [];
  zoom = 12;
  defaultLocation: google.maps.LatLngLiteral;
  options: google.maps.MapOptions = {
    zoomControl: true,
    scrollwheel: false,
    disableDoubleClickZoom: true,

    // maxZoom: 15,
    // minZoom:
  };
  pickupLoaction: google.maps.LatLngLiteral = null;
  dropLoaction: google.maps.LatLngLiteral = null;
  showMap = false;
  choosingPickup = false;
  choosingDrop = false;

  pickupInputValue = null;
  dropInputValue = null;

  customerName: string = null;
  customerPhone: string = null;
  customarAddress: string = null;
  itemModel: Item = {
    item: null,
    quantity: null,
    price: null,
  };

  items: Item[] = [];

  constructor(
    private loader: LoaderService,
    private authService: AuthService,
    private systemService: SystemService,
    private orderService: OrderService,
    public translate: TranslateService
  ) {}

  ngOnInit(): void {
    this.loadPaymentTypes();
    this.loadProductTypes();
    navigator.geolocation.getCurrentPosition((position) => {
      this.defaultLocation = {
        lat: position.coords.latitude,
        lng: position.coords.longitude,
      };
    });
  }

  stopProbagation(event: MouseEvent) {
    event.stopPropagation();
  }

  openMap() {
    this.showMap = true;
  }

  closeMap() {
    this.showMap = false;
  }

  choosePickup() {
    this.choosingPickup = true;
    this.openMap();
  }

  chooseDrop() {
    this.choosingDrop = true;
    this.openMap();
  }

  blur(e: Event) {
    (e.target as HTMLInputElement).blur();
  }

  mapClick(e: google.maps.MouseEvent) {
    if (this.choosingPickup) {
      this.pickupLoaction = e.latLng.toJSON();

      const marker = new google.maps.Marker({
        position: this.pickupLoaction,
        animation: google.maps.Animation.DROP,
      });

      this.markers[0] = marker;
      this.translate.get('newOrder.selected').subscribe((data) => {
        this.pickupInputValue = data;
      });

      console.log(this.pickupLoaction);
      console.log(this.dropLoaction);
    } else if (this.choosingDrop) {
      this.dropLoaction = e.latLng.toJSON();

      const marker = new google.maps.Marker({
        position: this.dropLoaction,
        animation: google.maps.Animation.DROP,
      });

      this.markers[0] = marker;
      this.translate.get('newOrder.selected').subscribe((data) => {
        this.dropInputValue = data;
      });

      console.log(this.pickupLoaction);
      console.log(this.dropLoaction);
    }
  }

  loadPaymentTypes() {
    this.paymentTypes$ = this.systemService.getPaymentTypes();
  }

  loadProductTypes() {
    this.productTypes$ = this.systemService.getProductTypes();
  }

  saveLocation() {
    this.choosingPickup = false;
    this.choosingDrop = false;
    this.closeMap();
    this.markers = [];
    console.log(this.pickupLoaction);
    console.log(this.dropLoaction);
  }

  resetMap() {
    this.markers = [];
    if (this.choosingPickup) {
      this.pickupLoaction = null;
      this.pickupInputValue = null;
      console.log(this.pickupLoaction);
      console.log(this.dropLoaction);
    } else if (this.choosingDrop) {
      this.dropLoaction = null;
      this.dropInputValue = null;
      console.log(this.pickupLoaction);
      console.log(this.dropLoaction);
    }
  }

  addItem() {
    //this.orderItem = new OrderItem();
    // orderItem.item = this.itemModel.item;
    // orderItem.quantity = this.itemModel.quantity;
    // orderItem.price = this.itemModel.price;
    // orderItem.description =

    if (
      this.orderItem.item &&
      this.orderItem.quantity &&
      this.orderItem.price &&
      this.orderItem.description
    ) {
      if (
        typeof this.orderItem.item === 'string' &&
        typeof this.orderItem.quantity === 'number' &&
        typeof this.orderItem.price === 'number' &&
        typeof this.orderItem.description === 'string'
      ) {
        this.orderItems.push(this.orderItem);
        this.orderItem = new OrderItem();

        // const item = {
        //   item: this.itemModel.item,
        //   quantity: this.itemModel.quantity,
        //   price: this.itemModel.price,
        // };
        // this.items.push(item);
      }
    }
    // this.itemModel.item = null;
    // this.itemModel.quantity = null;
    // this.itemModel.price = null;
  }

  deleteItem(index) {
    this.orderItems.splice(index, 1);
  }

  submit(successSwal: SwalComponent, errorSwal: SwalComponent) {
    // this.checkObjectPropertiesHaveNoValues(this.order);
    this.loader.showLoader$.next(true);
    this.initOrder();
    // const objectToApi = {
    //   AgentId: '1',
    //   PickupLocationLat: this.pickupLoaction.lat,
    //   PickupLocationLong: this.pickupLoaction.lng,
    //   DropLocationLat: this.dropLoaction.lat,
    //   DropLocationLong: this.dropLoaction.lng,
    //   CustomerName: this.customerName,
    //   CustomerPhone: this.customerPhone,
    //   CustomerAddress: this.customarAddress,
    //   OrderItems: this.items,
    // };

    this.orderService.submitOrder(this.order).subscribe(
      (data) => {
        if (data) {
          this.orderResult = data;
          this.loader.showLoader$.next(false);
          successSwal.fire();
          setTimeout(() => {
            document.querySelector('.content').scrollBy(0, 2000);
          }, 400);
        }
        // edit now
        else {
          this.loader.showLoader$.next(false);
          errorSwal.fire();
        }
      },
      (error) => {
        this.loader.showLoader$.next(false);
        errorSwal.fire();
      }
    );
    //console.log(this.order);
  }

  initOrder() {
    this.order.agentId = this.authService.getLoggedUser().id;
    this.order.pickupLocationLat = this.pickupLoaction.lat.toString();
    this.order.pickupLocationLong = this.pickupLoaction.lng.toString();
    this.order.dropLocationLat = this.dropLoaction.lat.toString();
    this.order.dropLocationLong = this.dropLoaction.lng.toString();
    this.order.orderItems = this.orderItems;
  }

  checkObjectPropertiesHaveNoValues(obj) {
    for (let key in obj) {
      if (obj[key] === null || obj[key] !== '' || obj[key] !== undefined)
        return true;
    }
    return false;
  }
}
